import React, { useState, useEffect } from "react";
import Sidebar from "../../../components/Admin/Sidebar";
import TopBar from "../../../components/Admin//TopBar";
import { useNavigate, useParams, Link } from "react-router-dom";
import axios from "axios";
import DataTable from "react-data-table-component";
import { Modal, Button } from "react-bootstrap";
import { FaArrowLeft, FaEye } from "react-icons/fa";
function AdminReferralUsage() {
  useEffect(() => {
    document.title = "Referral Usage- Admin";
  }, []);
  const [records, setrecords] = useState([]);
  const [searchQuery, setSearchQuery] = useState("");
  const apiUrl = "https://blueprintcatalyst.com/api/admin/module/";
  const { code } = useParams();
  useEffect(() => {
    if (code) {
      getallreferredUsage();
    }
  }, []);
  const getallreferredUsage = async () => {
    let formData = {
      code: code,
    };
    try {
      const res = await axios.post(apiUrl + "getallreferredUsage", formData, {
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json", // Ensure the content type is JSON
        },
      });
      var respo = res.data.results;
      setrecords(respo);
    } catch (err) {
      // Enhanced error handling
      if (err.response) {
      } else if (err.request) {
        console.error("Request data:", err.request);
      } else {
        console.error("Error message:", err.message);
      }
    }
  };
  const columns = [
    {
      name: "Referral Code",
      selector: (row) => row.discount_code,
      sortable: true,
      className: "age",
    },

    {
      name: "Used By Company",
      selector: (row) => row.company_name,
      sortable: true,
      className: "age",
    },

    {
      name: "Company Email",
      selector: (row) => row.email,
      sortable: true,
      className: "age",
    },
    {
      name: "Company Country",
      selector: (row) => row.company_country,
      sortable: true,
      className: "age",
    },
    {
      name: "Discount",
      selector: (row) => row.percentage + "%",
      sortable: true,
      className: "age",
    },
    {
      name: "Register Date",
      selector: (row) => {
        const date = new Date(row.registered_on);
        return date.toLocaleDateString("en-US", {
          year: "numeric",
          month: "short",
          day: "2-digit",
        });
      },
      sortable: true,
      className: "age",
    },
    {
      name: "Actions",
      cell: (row) => (
        <Link
          to={`/admin/setting/tracking/view/${row.discount_code}/${row.used_by_company_id}`}
          className="btn btn-sm btn-outline-primary"
          title="View Details"
        >
          <FaEye />
        </Link>
      ),
      ignoreRowClick: true,
      allowOverflow: true,
      button: true,
    },
  ];
  const filteredRecords = records.filter((record) =>
    Object.values(record)
      .join(" ")
      .toLowerCase()
      .includes(searchQuery.toLowerCase())
  );
  return (
    <>
      <div>
        <div className="d-flex align-items-start gap-0">
          <Sidebar />
          <div className="d-flex flex-column gap-0 w-100">
            <TopBar />
            <section className="dashboard_adminh">
              <div className="container-xl">
                <div className="row gy-4">
                  <div className="col-12">
                    <div className="card p-4">
                      <div className="d-flex justify-content-between mb-3">
                        <Link
                          to={`/admin/setting/paymentdiscountlist`}
                          className="btn btn-secondary py-2 my-3"
                          style={{ width: "fit-content" }}
                        >
                          <FaArrowLeft /> Back
                        </Link>
                        <h5 className="mb-3">Referral Usage</h5>
                      </div>
                      <div className="d-flex justify-content-end align-items-end">
                        <div class="search-bar">
                          <input
                            type="text"
                            placeholder="Search..."
                            value={searchQuery}
                            onChange={(e) => setSearchQuery(e.target.value)}
                          />
                          <span class="search-icon">
                            <svg
                              xmlns="http://www.w3.org/2000/svg"
                              fill="none"
                              viewBox="0 0 24 24"
                              stroke="currentColor"
                            >
                              <path
                                stroke-linecap="round"
                                stroke-linejoin="round"
                                stroke-width="2"
                                d="M21 21l-4.35-4.35M17 11a6 6 0 11-12 0 6 6 0 0112 0z"
                              />
                            </svg>
                          </span>
                        </div>
                      </div>
                      <DataTable
                        columns={columns}
                        data={filteredRecords}
                        pagination
                        highlightOnHover
                        striped
                        responsive
                        noDataComponent={
                          <div className="text-center">
                            <span>No results found</span>
                          </div>
                        }
                      />
                    </div>
                  </div>
                </div>
              </div>
            </section>
          </div>
        </div>
      </div>
    </>
  );
}

export default AdminReferralUsage;
